import Link from "next/link";
import {
  Activity,
  ArrowUpRight,
  RadioTower,
  Signal,
  Users,
  Zap,
} from "lucide-react";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { mockTowers } from "@/lib/mock-data";
import { Badge } from "@/components/ui/badge";
import StatCard from "@/components/stat-card";
import TowerCard from "@/components/tower-card";
import TowerStatusBadge from "@/components/tower-status-badge";
import { Button } from "@/components/ui/button";

export default function Dashboard() {
  const activeTowers = mockTowers.filter((t) => t.status === "active").length;
  const avgSignal = Math.round(
    mockTowers.reduce((acc, t) => acc + t.signalStrength, 0) /
      mockTowers.length
  );
  const totalUsers = mockTowers.reduce((acc, t) => acc + t.userLoad, 0);
  const totalEnergy = mockTowers.reduce((acc, t) => acc + t.energyConsumption, 0).toFixed(1);

  return (
    <div className="flex-1 space-y-4">
      <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
        <StatCard
          title="Active Towers"
          value={activeTowers.toString()}
          icon={<RadioTower className="h-4 w-4 text-muted-foreground" />}
          description={`${Math.round((activeTowers / mockTowers.length) * 100)}% of total`}
        />
        <StatCard
          title="Avg. Signal Strength"
          value={`${avgSignal} dBm`}
          icon={<Signal className="h-4 w-4 text-muted-foreground" />}
          description="Across all towers"
        />
        <StatCard
          title="Connected Users"
          value={totalUsers.toLocaleString()}
          icon={<Users className="h-4 w-4 text-muted-foreground" />}
          description="Real-time user load"
        />
        <StatCard
          title="Energy Consumption"
          value={`${totalEnergy} kWh`}
          icon={<Zap className="h-4 w-4 text-muted-foreground" />}
          description="Total this month"
        />
      </div>
      
      <Tabs defaultValue="grid">
        <TabsList>
          <TabsTrigger value="grid">Grid View</TabsTrigger>
          <TabsTrigger value="list">List View</TabsTrigger>
        </TabsList>
        <TabsContent value="grid">
            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4">
                {mockTowers.map((tower) => (
                    <TowerCard key={tower.id} tower={tower} />
                ))}
            </div>
        </TabsContent>
        <TabsContent value="list">
             <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Name</TableHead>
                    <TableHead>Status</TableHead>
                    <TableHead className="text-right">Signal (dBm)</TableHead>
                    <TableHead className="text-right">Users</TableHead>
                    <TableHead className="text-right">DL Speed (Mbps)</TableHead>
                     <TableHead className="text-right">Energy (kWh)</TableHead>
                    <TableHead>
                      <span className="sr-only">Actions</span>
                    </TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {mockTowers.map((tower) => (
                    <TableRow key={tower.id}>
                      <TableCell className="font-medium">{tower.name}</TableCell>
                      <TableCell>
                        <TowerStatusBadge status={tower.status} />
                      </TableCell>
                      <TableCell className="text-right">{tower.signalStrength}</TableCell>
                      <TableCell className="text-right">{tower.userLoad}</TableCell>
                      <TableCell className="text-right">{tower.downloadSpeed}</TableCell>
                       <TableCell className="text-right">{tower.energyConsumption}</TableCell>
                      <TableCell className="text-right">
                         <Button asChild variant="outline" size="sm">
                            <Link href={`/dashboard/towers/${tower.id}`}>
                                Details <ArrowUpRight className="ml-2 h-4 w-4" />
                            </Link>
                         </Button>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
        </TabsContent>
      </Tabs>
    </div>
  );
}
